/**
 * Returns first element of given array or `undefined` if array is empty.
 *
 * @param buf - array
 */
export const first = (buf) => buf[0];
/**
 * Returns last element of given array or `undefined` if array is empty.
 *
 * @param buf - array
 */
export const peek = (buf) => buf[buf.length - 1];
